* {
  box-sizing: border-box;
}

body {
  background: linear-gradient(90deg, black, gray, black);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  height: 100vh;
  width: 100vw;
}

.drum-machine {
  width: calc(100vw - 40vw);
  height: calc(100vh - 40vh);
  border-bottom: 4px solid #212121;
  margin: 10% auto;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  box-shadow: 10px -2px 20px 2px rgba(0, 0, 0, 30%);
  background: #212121;

  /* linear-gradient(217deg,
      rgba(27, 10, 10, 0.8),
      rgba(255, 0, 0, 0) 70.71%),
    linear-gradient(127deg, rgba(15, 49, 15, 0.8),
      rgba(55, 60, 55, 0) 70.71%),
    linear-gradient(336deg, rgba(22, 22, 71, 0.8),
      rgba(0, 0, 255, 0) 70.71%); */
}

.keypad {
  width: 60%;
  height: 100%;
  /* background-color: orange; */
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
}

.buttonHolder {
  width: calc(100% / 3);
  height: calc(100% / 3);
  background-color: #9c9a9a;
}

.pad-button {
  width: 99%;
  height: 99%;
  background-color: #eae4e4;
  font-size: calc(100vw - 97vw);
  text-align: center;
  padding: 25%;
  color: #eae4e4;
  cursor: pointer;
  border-left: 5px solid #212121;
  border-top: 5px solid #212121;
  box-shadow: 10px -2px 20px 2px rgba(0, 0, 0, 30%);
}

.pad-button:hover {
  color: #212121
}

.side-panel-container {
  width: 40%;
  height: 100%;
  /* background-color: cornflowerblue; */
}

.sidePanel {
  width: 50%;
  height: 60%;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  align-items: center;
  margin-right: auto;
  margin-left: auto;
  margin-top: 10%;
}

button {
  width: 30%;
  height: 17%;
  display: block;
  cursor: pointer;
  font-weight: 700;
  font-size: calc(100vw - 98.5vw);
  font-family: 'Courier New', Courier, monospace;
  border: 2px solid black
}

h1 {
  width: 100%;
  color: #9c9a9a;
  text-align: center;
  font-size: calc(100vw - 97.5vw);
}

#currentSound {
  width: 100%;
  height: 20%;
  border: 3px solid;
  text-align: center;
  font-weight: bold;
  font-size: calc(100vw - 98.3vw);
  background-color: #504e4e;
  cursor: default;
}

#creator {
  text-align: center;
  font-style: italic;
  font-size: calc(100vw - 99vw);
  color: #9c9a9a;
}

@media screen and (-webkit-min-device-pixel-ratio:0) {
  input[type='range'] {
    overflow: hidden;
    width: 100%;
    -webkit-appearance: none;
    background-color: #9c9a9a;
    border: 2px solid black;
  }

  input[type='range']::-webkit-slider-runnable-track {
    height: calc(100vh - 98.5vh);
    -webkit-appearance: none;
    color: #504e4e;
  }

  input[type='range']::-webkit-slider-thumb {
    width: 7%;
    -webkit-appearance: none;
    height: 100%;
    cursor: ew-resize;
    background: #434343;
    box-shadow: -80px 0 0 80px #504e4e;
    border: 1px solid black;
  }

}

/** FF*/
input[type="range"]::-moz-range-progress {
  background-color: #504e4e;
}

input[type="range"]::-moz-range-track {
  background-color: #9c9a9a;
}

/* IE*/
input[type="range"]::-ms-fill-lower {
  background-color: #434343;
}

input[type="range"]::-ms-fill-upper {
  background-color: #9c9a9a;
}

.speaker {
  width: calc(100vw - 92vw);
  height: calc(100vh - 87vh);
  border-radius: 50%;
  background-color: rgb(34, 29, 29);
  border: 5px solid black
}

.subSpeaker {
  width: 93%;
  height: 93%;
  margin: 2.5% auto;
  border-radius: 50%;
  background-color: rgb(61, 52, 52);
}

#volumeButton {
  cursor: pointer;
}

@media (max-width: 700px) {
  .drum-machine {
    width: 100vw;
    height: 100vh;
    display: flex;
    margin: 0;
  }

  .keypad {
    width: 100%;
    height: 60%;
    order: 2;
  }

  .side-panel-container {
    width: 100%;
    height: 40%;
    order: 1;
  }

  .sidePanel {
    width: 100%;
    height: 90%;
    margin-top: -10%;
  }

  button {
    width: calc(100vw - 80vw);
    font-size: calc(100vw - 94vw);
    order: 3;
    margin-bottom: -15%;
  }

  h1 {
    font-size: calc(100vw - 93vw);
    order: 1;
    padding: 0;
    margin: 0;
  }

  #currentSound {
    order: 5;
    margin-bottom: 0%;
    font-size: calc(100vw - 97vw);
  }

  #creator {
    font-size: calc(100vw - 97vw);
    margin-top: -3%;
  }

  #volumeButton {
    display: none;
  }

  .speaker {
    width: calc(100vw - 80vw);
    height: calc(100vh - 88vh);
    margin-bottom: -15%;
  }

  .subSpeaker {
    width: calc(100vw - 85vw);
    height: calc(100vh - 90vh);
    margin: 3% auto;
  }

  .pad-button:hover {
    color: #eae4e4;
  }
}